.card_container {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  padding-top: 30px;
  padding-bottom: 30px;
  justify-content: space-evenly;
}

.item_card {
  display: flex;
  flex-direction: column;
}

.item_card_info_container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 10px;
  flex-wrap: wrap;
  width: 350px;
  flex: auto;
}

.item_info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 1.5vw;
}
.item_image {
  flex: auto;
  width: 350px;
}

.item_name {
  font-family: "Anton";
  font-size: 1.5rem;
}

.item_price {
  font-family: "Roboto";
  font-size: 1.2rem;
}

.add_cart {
  background-color: black;
  border: none;
  color: white;
  padding: 10px;
  font-size: 0.7rem;
  transition: all;
  transition-duration: 0.5s;
  transition-timing-function: ease-in-out;
}

.add_cart:hover {
  background-color: white;
  border: black 1px solid;
  color: black;
  transform: scale(1.1);
}
