/*hero*/
.hero_container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  background-image: url(/src/img/hero_bg.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
}

.hero--text_container {
  padding-bottom: 15vh;
  padding-left: 5vw;
  overflow: hidden;
}
.hero--text1,
.hero--text2 {
  overflow: hidden;
}
.hero--text {
  font-family: "Bungee Shade", sans-serif;
  color: white;
  padding: 0;
  margin-top: 0;
  gap: 0;
  font-size: 7.5vw;
  letter-spacing: 0.1rem;
  display: block;
  animation: riseUp 1.5s cubic-bezier(0.77, 0, 0.175, 1);
}

@keyframes riseUp {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0%);
  }
}

.hero--subtext {
  font-family: "Roboto";
  color: white;
  font-size: 2.5vw;
  padding-top: 5px;
}

@media only screen and (max-width: 700px) {
  .hero_container {
    background-image: url(/src/img/hero_cropped.png);
  }

  .hero--text {
    font-size: 10vw;
  }

  .hero--subtext {
    font-size: 5vw;
    padding-top: 5px;
  }
}
