/*nav*/
.nav_container {
  padding-top: 20px;
  padding-bottom: 20px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 3;
  transition: background-color;
  transition-duration: 1s;
  transition-timing-function: ease-in-out;
}

.nav-bg {
  background-color: #b2afac;
}

.nav--logo_text {
  font-family: "Bungee Shade", cursive;
  font-size: 3rem;
  padding-left: 30px;
  background-color: transparent;
  border: none;
}
.nav--button_container {
  display: flex;
  align-items: center;
  gap: 20px;
  padding-right: 30px;
}

.nav--btn {
  font-size: 2.5rem;
  font-family: "Anton", sans-serif;
  color: white;
  border: none;
  background-color: transparent;
  transition: transform 0.2s ease-in-out;
}

.nav--btn:hover {
  border-bottom: 2px white solid;
  transform: scale(1.1);
}

.nav--shopping_icon {
  height: 3.5rem;
  transition: transform 0.2s ease-in-out;
}

.nav--shopping_icon:hover {
  transform: scale(1.1);
}

.nav--cart_container {
  display: flex;
  align-items: center;
}

.nav--cart_count_container {
  background-color: white;
  width: 2.5rem;
  height: 2.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
}

.nav--cart_count {
  font-size: 1.5rem;
  display: flex;
  justify-content: center;
}

@media only screen and (max-width: 700px) {
  .nav--logo_text {
    padding-left: 20px;
    display: flex;
    justify-content: flex-start;
  }

  .nav--button_container {
    display: flex;
    justify-content: flex-end;
    gap: 5px;
    padding-right: 20px;
  }

  .nav--home_btn {
    display: none;
  }

  .nav--shopping_icon {
    width: 3.5rem;
    transition: transform 0.2s ease-in-out;
  }
}
